        "El yakitori es una de las comidas callejeras más populares de Japón. Se trata de brochetas de pollo asadas a la parrilla, cocinadas generalmente sobre carbón vegetal y sazonadas con sal o con una salsa dulce-salada llamada tare. Este plato es especialmente común en los izakaya (bares tradicionales japoneses) y puestos callejeros, donde se sirve como acompañamiento para bebidas. Su simplicidad y sabor ahumado hacen del yakitori una experiencia gastronómica única, perfecta tanto como aperitivo como para una comida completa.",
